import axios from "axios";
import { useSelector } from "react-redux";
import BASE_URL from "../Config";


const getTotalApiDetails = async (token) => {

    try {
        const res = await axios.get(`${BASE_URL}dashboard`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const getCustomerList = async (token, entries,pageNumber, search,orderBy,sortedBy) => {
    try {
        const res = await axios.get(`${BASE_URL}customers?pageSize=${entries}&pageNumber=${pageNumber}&search=${search}&orderby=${orderBy}&sortby=${sortedBy}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const getCustomerById = async (token, id) => {
    try {
        const res = await axios.get(`${BASE_URL}getCustomer/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const addResidents = async (token,data) => {
    try {
        const res = await axios.post(`${BASE_URL}addResidents`,(data),{headers:{"Authorization":`Bearer ${token}`}})
        return res.data
    } catch (error) {
        console.log(error)
        throw(error)
    }
}
const updateCustomer = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}updateCustomer`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

