import axios from "axios"
import BASE_URL from "../Config"


const getPaymentList = async (token,entries,pageNumber) => {
    try {
        const res = await axios.get(`${BASE_URL}view_payment?pageSize=${entries}&pageNumber=${pageNumber}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getPaymentList }