import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet';
import ReactPaginate from 'react-paginate';
import { useSelector } from 'react-redux';
import BASE_URL from '../API/Config';
import { getPaymentList } from '../API/ManagePayment';
import Container from '../Component/Container'

const ManagePayment = () => {
  const [view, setView] = useState([])
  const [totalSize, setTotalSize] = useState(10)
  const [pageNumber, setPagenumber] = useState(1)
  const [example, setExample] = useState(false)
  const [customersCount, setCustomersCount] = useState();
  const [entries, SetEntries] = useState('10')
  // const token = useSelector(state => state.admin.adminlogintoken)
  const token = localStorage.getItem('admin_token')

  const Customerpayment = async () => {
    try {
      const total = await getPaymentList(token,entries, pageNumber)
      console.log('getPaymentList', total)
      setView(total.data.rows)
      setCustomersCount(total.data.count)
      setExample(true)
      const Count = total.data.count
      setTotalSize(Count / entries)
    } catch (error) {
      console.log('getPaymentList', error)
    }
  }

  const fecthComments = async (CurrentPage) => {
    const res = await fetch(
      `${BASE_URL}view_payment?pageSize=10&pageNumber=${CurrentPage}`, {
      headers: {
        "Authorization": `Bearer ${token}`
      },

      data: {
        pageSize: 10,
        pageNumber: CurrentPage
      }
    }
    );
    const data = await res.json();
    console.log(data)
    const d = data.data.rows;
    return d;
  }
  const Click = async (d) => {
    window.scrollTo(0, 0);
    var CurrentPage = d.selected + 1;
    setPagenumber(CurrentPage)
  };

  // const searchItems = (searchValue) => {
  //   setSearchInput(searchValue)
  //   if (view !== '') {
  //     const filteredData = view.filter((item) => {
  //       return Object.values(item).join('').toString().toLowerCase().includes(view.toLowerCase())
  //     })
  //     setFilteredResults(filteredData)
  //   }
  //   else {
  //     setFilteredResults(view)
  //   }
  // }

  useEffect(() => {
    Customerpayment()
  }, [pageNumber,entries])

  return (
    <Container>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Car Washing | Manage Payments</title>
      </Helmet>
      <div className="page-wrapper">
        <div className="page-header d-print-none">
          <div className="container-xl">
            <div className="row g-2 align-items-center">
              <div className="col">
                <h2 className="page-title">
                  Manage Payment
                </h2>
              </div>

              <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">Views</h3>
                  </div>
                  <div className="card-body border-bottom py-3">
                    <div className="d-flex">
                    <div className="text-muted">
                      Show
                      <div className="mx-2 d-inline-block">
                        <select className="form-control form-control-m" style={{ borderColor: 'rgb(0 0 0 / 21%)' }} onChange={(e) => {setPagenumber(1);SetEntries(e.target.value)}}>
                          <option value="10"> 10 entries</option>
                          <option value="15">15 entries</option>
                          <option value="20">20 entries</option>
                          <option value="25">25 entries</option>
                        </select>
                      </div>
                    </div>
                      <div className="ms-2 d-inline-block">
                        <input type="text" className="form-control form-control-m"
                          /* onChange={(e) => searchItems(e.target.value)} */
                          aria-label="Search invoice" placeholder=' Search' />
                      </div>
                    </div>
                  </div>
                  <div className="card-header">
                  <h4 className="text-muted" style={{ fontWeight: '400' }}>Showing {entries * (pageNumber - 1) + 1} to {(pageNumber * entries) > customersCount ? customersCount : pageNumber * entries } of {customersCount} entries</h4>
                </div>
                  <div className="table-responsive">
                    <table className="table card-table table-vcenter text-nowrap datatable" style={{ textTransform: 'capitalize' }}>
                      <thead>
                        <tr>
                          <th className="w-1">S No. </th>
                          <th>Amount</th>
                          <th>User Name</th>
                          <th>Payment Method</th>
                          <th>Transaction ID</th>
                          <th>Card Type</th>
                          <th>Created Date</th>
                        </tr>
                      </thead>
                      {/* {searchInput.length > 1 ? (
                        filteredResults.map((custom, index) => {
                          var timezone = new Date(custom.created_at).toLocaleString(undefined, { timeZone: 'Asia/Kolkata', hour12: true });
                          return (
                            <tbody key={index}>
                              <tr >
                                <td>{index + 1}</td>
                                <td> {custom.amount}</td>
                                <td> {custom.fullName}</td>
                                <td>{custom.payment_method}</td>
                                <td>{custom.transaction_id}</td>
                                <td>{custom.txn_type}</td>
                                <td style={{ textTransform: 'uppercase' }}>{timezone}</td>
                              </tr>
                            </tbody>
                          )
                        })
                      ) : */}
                      {view.map((custom, index) => {
                        let serial_num = ((entries * (pageNumber - 1)) + index + 1);
                        var timezone = new Date(custom.created_at).toLocaleString(undefined, { timeZone: 'Asia/Kolkata', hour12: true });
                        return (
                          <tbody key={index}>
                            <tr>
                              <td>{serial_num}</td>
                              <td>{custom.amount}</td>
                              <td>{custom.fullName}</td>
                              <td>{custom.payment_method}</td>
                              <td>{custom.transaction_id}</td>
                              <td>{custom.txn_type}</td>
                              <td style={{ textTransform: 'uppercase' }}>{timezone}</td>
                            </tr>
                          </tbody>
                        )
                      }
                      )
                      }
                      {example == true && view.length == 0 &&
                        <tbody>
                          <tr>
                            <td colSpan={7}>
                              <div className="empty">
                                <div className="empty-img"><img src="bluescarwash.png" height="128" alt="" />
                                </div>
                                <p className="empty-subtitle text-muted">No Data Available In Table</p>
                                <div className="empty-action">
                                </div>
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      }
                    </table>
                  </div>
                </div>
              </div>
            </div>
            <ReactPaginate
              style={{ background: 'red' }}
              previousLabel={"Previous"}
              nextLabel={'Next'}
              breakLabel={"..."}
              forcePage={pageNumber-1}
              pageCount={totalSize}
              marginPagesDisplayed={3}
              pageRangeDisplayed={3}
              onPageChange={Click}
              containerClassName={'pagination  justify-content-center mt-3'}
              pageClassName={'page-item'}
              pageLinkClassName={'page-link'}
              previousClassName={'page-item'}
              previousLinkClassName={'page-link'}
              nextClassName={'page-item'}
              nextLinkClassName={'page-link'}
              breakClassName={"page-item"}
              breakLinkClassName={'page-link'}
              activeClassName={'active'}
            />
          </div>
        </div>
      </div>

    </Container>
  )
}

export default ManagePayment
