import axios from "axios"

const getVehicles = async (id,token) => {
    try {
        let res = await axios(`https://tgu90.com:8000/admin/get_customer_vehicle/${id}`,{headers:{"Authorization":`Bearer ${token}`}})
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getVehicles }