import axios from "axios"
import BASE_URL from "../Config"


const getServiceProviders = async (token, entries,pageNumber, search, orderBy, sortedBy) => {
    try {
        const res = await axios.get(`${BASE_URL}service_providers_list?pageSize=${entries}&pageNumber=${pageNumber}&search=${search}&orderby=${orderBy}&sortby=${sortedBy}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const getServiceProviderById = async (token, id) => {
    try {
        const res = await axios.get(`${BASE_URL}service_providers_ById/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const UpdateServiceProvider = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}update_service_providers`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const addServiceProviders = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}add_service_providers`, (data), { headers: { "Authorization": `Bearer ${token}` ,'Content-Type':'multipart/form-data'} })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const deleteServiceProviders = async (token, id) => {
    try {
        const res = await axios.delete(`${BASE_URL}delete_service_providers/${id}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}
export { getServiceProviders, getServiceProviderById, UpdateServiceProvider, addServiceProviders, deleteServiceProviders }