import axios from "axios"
import BASE_URL from "../Config"


const getSchedualList = async (token, search, entries, check, pageNumber, orderBy, sortedBy) => {
    try {
        const res = await axios.get(`${BASE_URL}schedule_list/${check}?pageSize=${entries}&pageNumber=${pageNumber}&orderby=${orderBy}&sortby=${sortedBy}&search=${search}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const getServiceProviderslist = async (token,search) => {
    try {
        const res = await axios.get(`${BASE_URL}service_providers_list?pageSize=30&pageNumber=1&search=${search}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
       console.log(error)
       throw(error)
    }
}

const addAsignTask = async(token,data) => {
    try {
        const res = await axios.post(`${BASE_URL}assignTask`,(data),{headers:{"Authorization":`Bearer ${token}`}})
        return res
    } catch (error) {
        console.log(error)
        throw(error)
    }
}

export { getSchedualList ,getServiceProviderslist,addAsignTask}